//* Instncias: extenses da folha de estilos:
@use "vars"		 			as var;
@use "sass:color"			as color;
@use "sass:math"			as math;

/**
	*#	Px to Rem
	*	Converte pixels para o padro REM:
	*
	*	$value 				-> O valor em pixels.
*/
@function toRem($value)
{
	$value: ($value / var.$sREM) * 1rem;
	@return $value;
}

/**
	*#	Color
	*	Recupera uma cor de um array baseado na posio e no nome do array fornecido:
	*
	*	$array 				-> O vetor especfico.
	*	$value 				-> O nome da posio que est guardado o valor.
*/
@function color($array, $value)
{
  	@if map-has-key($array, $value)
	{
    	@return map-get($array, $value);
    }
  
  	@warn "O nome do vetor est incorreto ou no existe o valor que voc est buscando dentro dele.";
  	@return null;
}

/**
	*#	Color
	*	Recupera uma cor de um array baseado na posio e no nome do array fornecido:
	*
	*	$array 				-> O vetor especfico.
	*	$value 				-> O nome da posio que est guardado o valor.
*/
@function ctext($value)
{
  	@if map-has-key(var.$colStrings, $value)
	{
    	@return map-get(var.$colStrings, $value);
    }
  
  	@warn "O nome da cor informada no existe.";
  	@return null;
}